.scrollableArea {
    border: 0.5px solid;
    border-color: LightGray;
    padding: 15px;
    border-radius: 10px;
    width: 100%;
    height: 65vh;
    overflow-y: scroll;
    white-space: pre-line;
  }
#background {
  width: 100vw;
  position: fixed;
  left: 0;
  top: 30px;
  opacity: 0.8;
}
.text {
  position: absolute;
  z-index: 999;
  margin: 0 auto;
  left: 0;
  right: 0;
  text-align: center;
  top: 15vw;
  font-family: Impact, sans-serif;
}
.text h1 {
  font-size: 300%;
}
.iconsBox {
  display: -webkit-flex;
  display: flex;
  margin: auto;
  width: 28vw;
}
.icon {
  padding: 1vw;
  margin: 1vw;
  width: 12vw;
  border: 1px solid black;
  border-radius: 5px;
  background-color: white;
}
.linkText {
  color: black;
  background-color: white;
  display: inline-block;
  border-radius: 5px;
}

.middlePage {
  max-height: 80vh;
}
#chapter {
  border: 0.5px solid;
  border-color: LightGray;
  padding: 15px;
  border-radius: 10px;
  width: 100%;
  height: 77vh;
  overflow-y: scroll;
  white-space: pre-line;
}
#chapter h1 {
  font-size: x-large;
}
#vocabularyTitle {
  font-size: x-large;
}
#vocabulary {
  margin: 15px;
  width: 100%;
  height: 60vh;
  overflow-y: scroll;
}

.open .dropdown-menu {
  display: block;
  top: auto;
}
  
#aboutPage h1 {
  font-size: xx-large;
}

#aboutPage ul {
  display: block;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
